@keyframes fly {
  from {
    transform: translateZ(0px);
  }
  to {
    transform: translateZ(300px);
  }
}
@keyframes fade1 {
  from {
    opacity: .75;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade2 {
  from {
    opacity: 0;
  }
  to {
    opacity: .75;
  }
}
@keyframes flyFadeToZero {
  from {
    transform: translateZ(0px);
    opacity: 1;
  }
  to {
    transform: translateZ(300px);
    opacity: 0;
  }
}

/* Webkit */

@-webkit-keyframes fly {
  from {
    -webkit-transform: translateZ(0px);
  }
  to {
    -webkit-transform: translateZ(300px);
  }
}
@-webkit-keyframes fade1 {
  from {
    -webkit-opacity: .75;
  }
  to {
    -webkit-opacity: 1;
  }
}
@-webkit-keyframes fade2 {
  from {
    -webkit-opacity: 0;
  }
  to {
    -webkit-opacity: .75;
  }
}
@-webkit-keyframes flyFadeToZero {
  from {
    transform: translateZ(0px);
    -webkit-opacity: 1;
  }
  to {
    transform: translateZ(300px);
    -webkit-opacity: 0;
  }
}
